
header {
  width : 100%;
  background-color: white;
}

nav.nav-bar {
  lost-center: 1170px;
  height: 5rem;
  align-items: center;
}

.site-logo {
  lost-column: 6/12;
  order: 1;
  display: flex;
  align-self: baseline;
  position: relative;
  img {
    margin: auto;
   }
}

.nav-left-wrapper, .nav-right-wrapper {
  lost-column: 3/12;
  display: flex;
  justify-content: space-around;
}

.menu-item {
   display: block;
   font-family: $font-exo;
   text-decoration: none;
   font-size: 0.938rem;
   line-height: 1.27;
   text-transform: uppercase;
   color: $menu-text-color;
   position: relative;
     &::after {
       display: none;
       position: absolute;
       content: "";
       width: width("atom.png");
       height: height("atom.png");
       background: inline("atom.png");
       background-size: size("atom.png");
       top: 20px;
       left: 50%;
       transform: translateX(-7px);
     }
 }

 .menu-item:hover, .menu-item:focus {
   color: $menu-hover-color;
    &::after {
      display: block;
    }
 }

.nav-right-wrapper {
  order: 2;
}

.site-logo-text {
  margin: 0;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  font-size: 3.125rem;
  font-family: $font-exo;
  font-weight: bold;
  text-transform: uppercase;
  color:$menu-hover-color;

    & span {
      font-size: 2.063rem;
      color: $menu-text-color;
    }
}
